<template>
  <div class="content">
    <div class="header">
        <div class="newContent">
        <div class="template">
          <div class="num">12.00</div>
          <div class="title">账户余额(元)</div>
        </div>
        <div class="template">
          <div class="num">2</div>
          <div class="title">银行卡(张) <div class="icon"><img src="@/assets/personal/leftJ.png" alt=""></div></div>
        </div>
        <div class="template">
          <div class="btnT" @click="jumpCash">提现记录
              <van-icon name="arrow"></van-icon>
          </div>
        </div>

      </div>
      
    </div>
    <div class="detailCash">
      <div class="cash">
        <div class="cashTitle">请选择提现方式</div>
        <div class="cashStyle">
          <div class="styleBtn" :class="{styleBtnColor:changeSelectStyle === 0}" @click="choosePay(0)">
            <div class="icon"></div>
            <div class="span">提现至微信零钱</div>
          </div>
          <div class="styleBtn" :class="{styleBtnColor:changeSelectStyle === 1}" @click="choosePay(1)">
            <div class="icon icon_play"></div>
            <div class="span">提现至银行卡</div>
          </div>

        </div>

        <div class="cont">
          <div class="newContent">提现金额(元)</div>
          <div class="input">
            <van-field v-model="number" placeholder="可提现余额 200元" />
            
          </div>
          <div class="allput">全部体现</div>
          <div class="fooBtn">提现</div>
        </div>
        <div class="contentNew">温馨提示：申请提现后，会自动扣除8%手续费</div>
        <!--银行卡提现提示-->
        <div class="cardTip">
          <van-popup v-model="isCardTip">
            <div class="close" @click="chooseNo"><img src="@/assets/personal/no1.png" alt=""></div>
            <div class="title">您还未绑定任何银行卡</div>
            <div class="cardBtn" @click="jumpAddCard">去添加银行卡</div>
          </van-popup>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
    data(){
          return{
            changeSelectStyle:0,
            number:'',
            isCardTip:false,

          }
      },
      created(){
        console.log(this.$route.query.json)
        // console.log()
        document.documentElement.style.backgroundColor = '#fff'
      },
    methods:{
        
        jumpCash(){
          const json={
            name:'3434',
            id:121
          }
          this.$router.push({ path:'/personalcenter/detailCash',query: {json:json} })
        },
        choosePay(val){
          if(val=1){
            this.isCardTip=true;
          }
          this.changeSelectStyle=val
        },
        chooseNo(){
          this.isCardTip=false
          this.changeSelectStyle=0
        },
        jumpAddCard(){
          this.$router.push({ path:'/personalcenter/bankCard',query: {} })
        },
      }
};
</script>

<style lang="scss" scoped>
:root{
    width: 100%;
    height: 100%;
    background: #fff;
}
.content{
    background: #fff;
    .header{
        background: url('../../../assets/personal/bg-t.png');
        height: 280px;
        box-sizing: border-box;
        width: 100%;
        padding: 55px 45px 114px 50px;
        background-size: 100% 100%;
        .newContent{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .template{
          display: inline-table;
          text-align: center;
          color:rgba(255,255,255,1);
          .num{
            height: 75px;
            font-size:56px;
            line-height: 75px;
          }
          .title{
            font-size:24px;
            line-height: 33px;
            .icon{
              width: 30px;
              height: 30px;
              float: right;
              padding-top: 2px;
              box-sizing: border-box;
              img{
                width: 100%;
              }
            }
          }
          .btnT{
            width: 160px;
            height: 52px;
            justify-content: center;
            background:rgba(255,255,255,1);
            border-radius:60px;
            font-size:26px;
            line-height: 52px;
            color: #9F3137;
            display: flex;
            align-items: center;
            margin-top: 47px;
          }
        }

      }
    }
    .detailCash{
  
    background: white;
    .cash{
      width: 100%;
      padding: 30px;
      box-sizing: border-box;
      .cashTitle{
        font-size:28px;
        color:rgba(34,34,34,1);
        line-height: 40px;
        margin-bottom: 30px;
      }
    }
    .cashStyle{
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
      .styleBtn{
        border:2px solid rgba(0,0,0,0);
        display: inline-table;
        padding: 22px 35px;
        box-sizing: border-box;
        box-shadow:0px 1px 8px rgba(0,0,0,0.08);
        border-radius:10px;
        div{
          display: inline-table;
          float: left;
        }
        .icon{
          width: 44px;
          height: 44px;
          margin-right: 20px;
          background: url("../../../assets/personal/slice.png") no-repeat;
          background-size: 100% 100%;
        }
        .icon_play{
          width: 50px;
          height: 39px;
          margin-top: 3px;
          background: url("../../../assets/personal/slice_play.png") no-repeat;
          background-size: 100% 100%;
        }
        .span{
          display: inline-table;
          font-size:28px;
          line-height: 44px;
          color:rgba(85,85,85,1);

        }
      }
      .styleBtnColor{
        border:2px solid rgba(253,101,109,1);
        .span{
          color:rgba(253,101,109,1);
        }
      }
    }
    .cont{
      width: 100%;
      box-shadow:0px 1px 8px rgba(0,0,0,0.08);
      padding: 29px 30px 46px 30px;
      box-sizing: border-box;
      color: #222222;
      .newContent{
        font-size:28px;
        line-height: 40px;
        margin-bottom: 20px;
      }
      .input{
        width: 100%;
        height: 100px;
        border-bottom: thin solid #D7D7D7;
        margin-bottom: 20px;

        .van-cell{
          display: inline-table;
          width: 80%;
          font-size: 28px;
          padding: 25px 10px;
        }
        
        .van-cell:not(:last-child)::after{
          border: 0;
        }
        .span{
          display: inline-table;
          float: right;
          line-height: 100px;
        }
      }
      .Reason{
        width: 100%;
        font-size:24px;
        line-height: 33px;
        color:rgba(34,34,34,1);
        margin-bottom: 52px;
        span{
          float: right;
          color: #FD656D;
        }
      }
      .allput{
          color: #FF4A54;
          font-size: 24px;
          margin-bottom: 55px;
          margin-left: 10px;
      }
      .fooBtn{
        width: 100%;
        height: 80px;
        line-height: 80px;
        text-align: center;
        border-radius:60px;
        background:rgba(253,101,109,1);
        font-size:30px;
        color:rgba(255,255,255,1);
      }
    }
    .contentNew{
      margin-top: 40px;
      width: 100%;
      text-align: center;
      color: #888888;
      font-size:24px;
      line-height: 33px;
    }
    .cardTip{
      .van-popup{
        width: 67.93%;
        display: inline-table;
        border-radius: 10px;
        text-align: center;
        position: relative;
        .close{
          width: 40px;
          height: 40px;
          position: absolute;
          right: 20px;
          top: 20px;
          img{
            width: 100%;
          }
        }
        .title{
          width: 100%;
          padding: 95px  0;
          box-sizing: border-box;
          color: #222222;
          font-size: 30px;
          line-height: 42px;
        }
        .cardBtn{
          width: 100%;
          height: 100px;
          line-height: 100px;
          font-size:30px;
          border-top:1px solid rgba(229,229,229,1);
          color:rgba(253,101,109,1);
        }
      }

    }
  }
}
</style>


